/** @jsx jsx */
import React from 'react';
import { css, jsx } from '@emotion/core';

const TimeComponent = ({ label, value, className }) => (
  <div
    css={theme => css`
      width: 5.625em;
      height: 5.625em;
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      border: 1px solid rgba(255, 255, 255, 0.8);
      border-radius: 50%;
      @media (max-width: ${theme.breakpoints.sm}) {
        width: 4.375em;
        height: 4.375em;
      }
    `}
    className={className}
  >
    <div
      css={theme => css`
        font-size: 1.375em;
        font-weight: 900;
      `}
    >
      {value}
    </div>
    <span
      css={theme => css`
        font-size: 0.75em;
        font-weight: 500;
        color: rgba(255, 255, 255, 0.95);
      `}
    >
      {label}
    </span>
  </div>
);

const Clock = ({ target }) => {
  const [remainingSeconds, setRemainingSeconds] = React.useState(0);

  React.useEffect(() => {
    const updateRemainingSeconds = () => {
      const now = new Date();
      if (now < target) {
        setRemainingSeconds((target - now) / 1000);
      }
    };
    const interval = setInterval(updateRemainingSeconds, 1000);
    updateRemainingSeconds();
    return () => clearInterval(interval);
  }, [target]);

  return (
    <div
      css={theme => css`
        display: flex;
        & > *:not(:first-of-type) {
          margin-left: 0.75em;
          @media (max-width: ${theme.breakpoints.sm}) {
            margin-left: 0.5em;
          }
        }
      `}
    >
      <TimeComponent
        label="days"
        value={Math.floor(remainingSeconds / 86400)}
      />
      <TimeComponent
        label="hours"
        value={Math.floor((remainingSeconds % 86400) / 3600)}
      />
      <TimeComponent
        label="mins"
        value={Math.floor((remainingSeconds % 3600) / 60)}
      />
      <TimeComponent label="secs" value={Math.floor(remainingSeconds % 60)} />
    </div>
  );
};
Clock.defaultProps = {
  target: null,
};

export default React.memo(Clock);
